import { Component, OnInit, OnDestroy, AfterViewInit, OnChanges } from '@angular/core';
import { BaseComponent } from '../../base/base.component';
import { BOTONES_FLUJO, RESTRICT, MODAL, PAIS, MODULO, LENGUAJE } from 'src/mbaas/mbaas.const';
import { CurrencyPipe } from '@angular/common';
import { FormArray, FormGroup } from '@angular/forms';
import { CardsPipe } from 'src/mbaas/core/pipes/cards/cards.pipe';
import { Modal } from 'src/mbaas/core/components/modal/modal';
import { AlertModalTMAComponent } from 'src/mbaas/core/components/modal/alert-modal-tma/alert-modal-tma.component';
import { forkJoin } from 'rxjs';
import { CAT_BINES } from 'src/mbaas/mbaas.kind.const';

@Component({
  selector: 'mbaas-ccc001',
  templateUrl: './ccc001.component.html',
  styleUrls: ['./ccc001.component.scss']
})
export class Ccc001Component extends BaseComponent implements OnInit, OnDestroy {
  show = false;
  producto: any;
  items: FormArray;
  restrict = RESTRICT;
  catalogData = { BINES: [] };
  cardValues: { [key: string]: string };
  isModalOpen = false;

  ngOnInit() {
    this.cardValues = {
      VALOR_MAX_COMPRA_CARTERA: this.payload.valorMaximoCompraCartera,
      VALOR_MAX_COMPRA_CARTERA_DISPONIBLE: this.payload.valorMaximoCompraCartera,
      VALOR_COMPRA_CARTERA: this.payload.cupoCompraCartera,
      VALOR_SIN_COMPRA_CARTERA: this.payload.cupoSinCompraCartera
    };
    forkJoin(this.catalogs([{ kind: CAT_BINES, params: [PAIS, MODULO, LENGUAJE] }])).subscribe(this.setRules());
  }

  test() {
    console.log('formulario values: ', this.formulario.getRawValue());
    console.log('formulario values cleaned: ', this.formData());
    console.log('formulario: ', this.formulario);
  }

  getDataToBack(): () => { [key: string]: any } {
    return (): { [key: string]: any } => ({
      atras: BOTONES_FLUJO.ATRAS
    });
  }

  getDataToContinue(): () => { [key: string]: any } {
    const form = this.formData();
    form.productos.filter(producto => producto.seleccionado).forEach(producto => {
      delete producto.valorMinimoCompra;
      delete producto.valorMaximoCompra;
    });
    return (): { [key: string]: any } => ( form );
  }

  setRules() {
    return response => {
      const R = this.rules;
      this.catalogData.BINES = response[0];
      super.setRules({
        productos: this.formBuilder.array([], R.validationForMinRequired('seleccionado')),
        cupoTotalTarjetas: ['', []],
        valorTotalCompraCartera: ['', [
          R.validationForMaxNumber(Number(this.cardValues.VALOR_MAX_COMPRA_CARTERA))
        ]],
      });
      this.producto = this.payload.productos;
      this.producto.forEach((item) => {
        this.items = this.formulario.get('productos') as FormArray;
        this.items.push(this.createDueItem(item));
      });
      this.formulario.patchValue(this.payload);
      this.markAllAsTouched();
      this.formulario.get('productos').valueChanges.subscribe(() => {
        this.setValorTotalCompraCartera();
        this.setValorTotalCompraCarteraDisponible();
      });
      this.formulario.get('valorTotalCompraCartera').valueChanges.subscribe(() => this.setCupoTotalTarjetas());
      this.show = true;
    };
  }

  createDueItem(producto: any): FormGroup {
    const R = this.rules;
    return this.formBuilder.group({
      idProducto: [producto.idProducto, []],
      seleccionado: [false, [
        R.resetMyCallbackValidation(['valorCompraCartera', 'numeroTarjeta'])
      ]],
      valorCompraCartera: ['', [
        R.validationForRequired(),
        R.validationForMinNumber(producto.valorMinimoCompra),
        R.validationForMaxNumber(producto.valorMaximoCompra),
        R.disbaleControl((control: FormGroup) => {
          return !control.parent.get('seleccionado').value;
        })
      ]],
      numeroTarjeta: ['', {
        validators: [
          R.validationForRequired(),
          R.validationForBines(this.catalogData.BINES),
          R.disbaleControl((control: FormGroup) => {
            return !control.parent.get('seleccionado').value;
          })
        ],
      }
      ],
      valorMinimoCompra: [[]],
      valorMaximoCompra: [[]]
    });
  }

  /*
    * Se activan los errores de las obligaciones seleccionadas
  */
  markAllAsTouched(): void {
    this.formulario.get('valorTotalCompraCartera').markAsTouched();
    (this.formulario.get('productos') as FormArray).controls
    .filter(x => x.get('seleccionado').value === true)
    .forEach(y => y.markAllAsTouched());
  }

  /* Mascara para tipo de moneda */
  maskNumbers(value: string): string {
    return new CurrencyPipe('en-US').transform(value, 'USD', '$ ', '.2');
  }

  /* Mascara para tipo de card */
  maskCards(value: string): string {
    return new CardsPipe().transform(value);
  }

  /*
    * Retorna TRUE si alguna obligacion está seleccionada
  */
  chakOblicaciones(): boolean {
    return (this.formulario.get('productos') as FormArray).getRawValue().find(x => x.seleccionado === true);
  }

  /*
    * Revisa si el valor mínimo de la tarjeta es menor que el saldo restante
      del cupo compra cartera
  */
  validationForValorCompraCarteraDisponible(formGroup: FormGroup): boolean {
    if (formGroup.get('seleccionado').value === true) {
      return false;
    }
    return !((Number(formGroup.get('valorMinimoCompra').value) * 0.90) <= Number(this.cardValues.VALOR_MAX_COMPRA_CARTERA_DISPONIBLE));
  }

  /*
    * Evento que se lanza al perder el foco en el input del saldo actual de la tarjeta
    * Valida que este dentro del rango (valorMinimoCompra, valorMaximoCompra)
    * Muestra un modal con un mensaje de error en caso de no estar en el rango
  */
  validForValorCompraCartera(formGroup: FormGroup, event?: any): void {
    const P = formGroup.getRawValue();
    if (this.isModalOpen || !event || (event.relatedTarget !== null && event.relatedTarget.type === 'checkbox')
      || this.isBetween(P.valorCompraCartera, P.valorMinimoCompra, P.valorMaximoCompra)) {
      return;
    }
    const errorMessage
      = (Number(P.valorCompraCartera) < Number(P.valorMinimoCompra)
        ? this.translate.instant('modal.valorMinimoCompra')
        : this.translate.instant('modal.valorMaximoCompra'))
        .replace('${minValue}', this.maskNumbers(P.valorMinimoCompra))
        .replace('${maxValue}', this.maskNumbers(P.valorMaximoCompra));
    const modal: Modal = {
      display: true,
      buttons: [
        {
          callback: (close) => {
            P.valorCompraCartera = Number(P.valorCompraCartera) < Number(P.valorMinimoCompra)
              ? formGroup.get('valorCompraCartera').setValue(P.valorMinimoCompra)
              : formGroup.get('valorCompraCartera').setValue(P.valorMaximoCompra);
            this.isModalOpen = false;
            close();
          },
          buttonText: 'Aceptar',
          class: 'button button--primary'
        }
      ],
      title: '',
      message: errorMessage,
      entryComponent: AlertModalTMAComponent
    };
    this.obser.sendData(modal, MODAL);
    this.isModalOpen = true;
  }

  isBetween(value: any, min: any, max: any): boolean {
    return Number(value) >= Number(min) && Number(value) <= Number(max);
  }

  /*
    * Sumatoria del saldo ingresado de las tarjetas seleccionadas
  */
  setValorTotalCompraCartera(): void {
    const form = (this.formulario.get('productos') as FormArray).getRawValue();
    const value = form.filter(x => x.seleccionado === true).reduce((previusValue, currenValue) => {
      currenValue.valorCompraCartera = parseInt(currenValue.valorCompraCartera || '0', 10) +
        parseInt(previusValue.valorCompraCartera, 10);
      return currenValue;
    }, {
      valorCompraCartera: '0'
    }).valorCompraCartera;
    this.formulario.get('valorTotalCompraCartera').setValue(value.toString());
  }

  /*
    * Saldo restante del cupo de compra cartera
  */
  setValorTotalCompraCarteraDisponible(): void {
    const form = (this.formulario.get('productos') as FormArray).getRawValue();
    const value = form.filter(x => x.seleccionado === true).reduce((previusValue, currenValue) => {
      currenValue.valorMinimoCompra = Number(previusValue.valorMinimoCompra || '0') -
        Number(currenValue.valorMinimoCompra || '0');
      return currenValue;
    }, {
      valorMinimoCompra: Number(this.cardValues.VALOR_MAX_COMPRA_CARTERA)
    }).valorMinimoCompra;
    this.cardValues.VALOR_MAX_COMPRA_CARTERA_DISPONIBLE = value;
  }

  /*
    * Valor total de la tarjeta
    * Si la suma es mayor al cupo con compra de cartera, muestra el cupoCompraCartera
  */
 setCupoTotalTarjetas(): void {
    const valorTotalCompraCartera = Number(this.formulario.get('valorTotalCompraCartera').value);
    const sum = valorTotalCompraCartera + Number(this.cardValues.VALOR_SIN_COMPRA_CARTERA);
    const value = sum > Number(this.cardValues.VALOR_COMPRA_CARTERA)
      ? Number(this.cardValues.VALOR_COMPRA_CARTERA) : sum;
    this.formulario.get('cupoTotalTarjetas').setValue(value.toString());
  }

  /* Mensajes de error personalizados */
  get messagesForProductos(): Array<{ [key: string]: string }> {
    return [{ type: 'minRequired', message: 'Selecciona al menos una obligación.' }];
  }

  get messagesForNumeroTarjeta(): Array<{ [key: string]: string }> {
    return [
      { type: 'bin', message: 'El bin ingresado es incorrecto.' },
      { type: 'bankType', message: 'Solo se aceptan bancos nacionales.' },
    ];
  }

}
