import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from '../../base/base.component';
import { BOTONES_FLUJO } from 'src/mbaas/mbaas.const';

@Component({
  selector: 'mbaas-ccc003',
  templateUrl: './ccc003.component.html',
  styleUrls: ['./ccc003.component.scss']
})
export class Ccc003Component extends BaseComponent  implements  OnInit, OnDestroy {


  ngOnInit() {
  }

  ngOnDestroy() {
  }

  getDataToBack(): () => { [key: string]: any } {
    return (): { [key: string]: any } => ({
      atras: BOTONES_FLUJO.ATRAS
    });
  }

  getDataToContinue(): () => { [key: string]: any } {
    return (): { [key: string]: any } => ({
      atras: BOTONES_FLUJO.CONTINUAR,
      formulario: 'this.getFormData()'
    });
  }

}
